<template>
  <div>
    <v-header :isUserInfo="false" :isOther="true" :isRegister="true" />
    <div class="web">
      <Form
        :model="registerInfo"
        :rules="registerRules"
        :label-width="100"
        class="register-box"
        ref="registerForm"
      >
        <FormItem label="用户名" prop="userName">
          <Input v-model="registerInfo.userName" placeholder="请设置用户名" />
        </FormItem>
        <FormItem label="密码" prop="pwd">
          <Input type="password" v-model="registerInfo.pwd" placeholder="请设置登录密码" />
        </FormItem>
        <FormItem label="确认密码" prop="rePwd">
          <Input type="password" v-model="registerInfo.rePwd" placeholder="请确认密码" />
        </FormItem>
        <FormItem label="手机号" prop="tel">
          <Input v-model="registerInfo.tel" placeholder="可用于登录和找回密码" />
        </FormItem>
        <FormItem label="验证码" prop="code">
          <Row>
            <Col span="16">
              <Input v-model="registerInfo.code" placeholder="请输入验证码" />
            </Col>
            <Col span="7" offset="1">
              <Button :disabled="!isGetCode" type="primary" @click="getTelCode">{{getCodeTip}}</Button>
            </Col>
          </Row>
        </FormItem>
        <FormItem class="agreement">
          <Checkbox v-model="isAgreement">
            <span>阅读并接受</span>
            <router-link to>《盛大健康用户协议》</router-link>
            <span>及</span>
            <router-link to>《盛大健康隐私保护声明》</router-link>
          </Checkbox>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="register">注册</Button>
          <Button @click="reset" style="margin-left: 8px">重置</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
import * as validate from "@/utils/verification";
export default {
  data() {
    return {
      registerInfo: {
        userName: "",
        pwd: "",
        rePwd: "",
        tel: "",
        code: ""
      },
      registerRules: {
        userName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        pwd: [{ required: true, validator: this.validatePwd, trigger: "blur" }],
        rePwd: [
          { required: true, validator: this.validateRePwd, trigger: "blur" }
        ],
        tel: [{ required: true, validator: validate.phone, trigger: "blur" }],
        code: [{ required: true, message: "请输入验证码", trigger: "blur" }]
      },
      isGetCode: true, // 可不可获取验证码
      getCodeTip: "获取验证码",
      isAgreement: false //是否同意协议
    };
  },
  methods: {
    // 验证密码
    validatePwd(rule, value, callback) {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.registerInfo.rePwd !== "") {
          // 对第二个密码框单独验证
          this.$refs.registerForm.validateField("rePwd");
        }
        callback();
      }
    },
    // 验证第二次密码
    validateRePwd(rule, value, callback) {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.registerInfo.pwd) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    },
    // 获取验证码
    getTelCode() {
      if (this.isGetCode && !this.registerInfo.tel) return;
      this.isGetCode = false;
      this.$api
        .getTelCode({
          moblie: this.registerInfo.tel,
          type: "0001"
        })
        .then(res => {
          let timer = 0;
          let seconde = 0;
          clearInterval(timer);
          timer = setInterval(() => {
            if (seconde == 60) {
              clearInterval(timer);
              this.getCodeTip = "重新获取";
              this.isGetCode = true;
            } else {
              seconde += 1;
              this.getCodeTip = `${60 - seconde}s`;
            }
          }, 1000);
        })
        .catch(err => {
          console.log(err);
          this.isGetCode = true;
        });
    },
    // 注册
    register() {
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          if (this.isAgreement) {
            this.$api
              .register({
                mobile: this.registerInfo.tel,
                password: this.registerInfo.pwd,
                verifyCode: this.registerInfo.code
              })
              .then(res => {
                // console.log(res);
                this.$Notice.info({ title: "恭喜您，注册成功" })
              })
              .catch(err => console.log(err));
          } else {
            return this.$Notice.info({ title: "请阅读并接受相关协议" });
          }
        }
      });
    },
    // 重置
    reset() {
      this.$refs.registerForm.resetFields();
    }
  }
};
</script>

<style lang="less" scoped>
@import "~@/less/mixins.less";
.web {
  position: relative;
  overflow: hidden;

  &:after {
    content: "";
    .xcenter();
    width: 100%;
    top: 0;
    height: 2px;
    background-color: @borderColor;
    box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.08);
  }
}
.register-box {
  width: 425px;
  margin: 80px 0 0 30px;

  .agreement {
    * {
      vertical-align: middle;
      line-height: 20px;
    }
    a {
      color: @themeColor;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
</style>


